/* 
/* timer */
.container{
width: 100%;
display: flex;
flex-direction: column;
margin-top: 15px;
    
}
.title{
    width: auto;
    text-align: center;
    /* width: 100%;  */
    /* text-algn: scenter; */
    
}
#clockdiv{ 
    font-family: sans-serif; 
    color: #fff; 
    display: inline-block; 
    font-weight: 100; 
    text-align: center; 
    font-size: 30px; 
    margin-top: -10px;
} 
#clockdiv > div{ 
    padding: 10px; 
    border-radius: 3px; 
    display: inline-block; 
} 
#clockdiv div > span{ 
    width: 56px;
    height: 47px;
    border-radius: 3px;
    background: #eee;
    display: inline-block;
    color: #333e48;
} 
.smalltext{ 
    padding-top: 5px; 
    font-size: 16px; 
    color: #333e48;
} 